<template>
    <div class="goods">
        <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
        <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
        <div class="demonstration-box">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="pageParams.size"
                layout="total, prev, pager, next"
                :total="pageParams.totalCount"
            ></el-pagination>
        </div>
        <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
            <span>{{dialogContent}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="sure">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import Options from "views/common/options";
import Tables from "views/common/tables";
import queryString from "querystring";
import { getSchool, getClass } from 'data/listBody';
import { ScondDay } from 'utils/formData';
import page from "data/page";

const dataOption = {
    leftMenu: [
        { id:'1', name:'校区：', placeholder:'请选择校区名称', module:'schoolId', type:'select'},
        { id:'2', name:'班级：', placeholder:'请选择班级名称', module:'classId', type:'select'},
        // {
        //     id: "3",
        //     name: "带班老师：",
        //     placeholder: "请输入带班老师",
        //     module: "price"
        // },
        // {
        //     id: "4",
        //     name: "学员姓名：",
        //     placeholder: "请输入学员姓名",
        //     module: "count"
        // },
        // {
        //     id: "5",
        //     name: "审批人：",
        //     placeholder: "请输入审批人",
        //     module: "count"
        // },
        // {
        //     id: "6",
        //     name: "审批时间：",
        //     placeholder: "请选择审批时间",
        //     module: "beginTime",
        //     type: "date"
        // }
    ],
    rightMenu: []
};

const dataTable = {
    table: [
        { label: "校区", prop: "schoolName" },
        { label: "班级", prop: "className" },
        { label: "带班老师", prop: "teacherName" },
        { label: "学员", prop: "userName" },
        // { label: "审批人", prop: "person" },
        // { label: "审批时间", prop: "beginTime" },
        // { label: "类型", prop: "type" },
        { label: "积分", prop: "score" },
        { label: "时间", prop: "createTimes" },
        { label: "备注", prop: "remark" },
    ],
    edit: [{ name: "拒绝", id: "t1" }]
};

const tips = ["确认拒绝该审批?"];

export default {
    name: "integral",
    mixins: [page],
    data() {
        return {
            dataOption: dataOption,
            dataTable: JSON.parse(JSON.stringify(dataTable)),
            dialogVisible: false,
            dialogContent: tips[0],
            tableList: [],
            loading: false,
            currentPage: 1,
            id: "",
            type: "",
        };
    },
    components: {
        Options,
        Tables
    },
    mounted() {
        this.init();
        this.search();
        this.dropInit();
    },
    activated(){
        this.search();
    },
    methods: {
        init() {
            const { type, id } = this.$route.query;
            this.id = id;
            this.type = type;
            if (type === "detail"){ //积分明细进入
                this.dataOption.rightMenu = [
                    { id: "r1", name: "查询" },
                    { id: "r2", name: "取消" }
                ];
                return this.dataTable.edit = [];
            } 
            this.dataOption.rightMenu = [
                { id: "r1", name: "查询" },
            ];
            this.dataTable = dataTable;
        },
        dropInit() {
            getSchool(res => {
                let { leftMenu } = this.dataOption;
                leftMenu = leftMenu.map(item => {
                    if (item.module === "schoolId") item.options = res;
                    return item;
                });
                this.dataOption.leftMenu = leftMenu;
            });
        },
        optchange(params) {
            console.log(params.id + "--id被单击了");
            if (params.id === "r1") return this.search();
            if (params.id === "r2") return this.$router.go(-1);
        },
        tabchange(params) {
            console.log(params.id + "--id被单击了");

            this.id = params.data.id;
            // 拒绝
            if (params.id === "t1") return (this.dialogVisible = true);
        },
        sure(){
            this.api.delete(`/score/${this.id}`).then((res)=>{
                this.loading = false;
                this.search();
                this.$message(res.data);
            }).catch((err) => {
                console.log(err)
                this.$message(err.data.message)
            })
            this.dialogVisible = false;
        },
        search(){  //搜索
            const optParams = this.$refs.goodsOpt.getData();
            const pageParams = { size: 20, page: this.currentPage -1 };
            let params = Object.assign({}, optParams, pageParams);
            // if(this.type === 'detail'){
            //     params = Object.assign({}, params, {userId: this.id});
            // }
            params = Object.assign({}, params, {userId: this.id});
            const param = queryString.stringify(params || {});
            this.loading = true;
            this.api.get(`/score?${param}`).then((res)=>{
                this.loading = false;
                this.dataInit(res.data);
                this.setPage(res.data);
                this.tableList = res.data.list;
            }).catch((err) => {
                console.log(err)
                this.$message(err.data.message)
            })
        },
        dataInit(data){//数据初始化
            data.list = data.list.map(item => {
                item.createTimes = ScondDay(item.createTime);
                return item;
            })
            this.tableList = data.list;
        }
    }
};
</script>
 
<style lang="less" scoped>
</style>
 